.counterBox {
  background-color: black;
  width: fit-content;
  margin: 0 20px;
  border-radius: 10px;
  border: 2px solid brown;
  box-sizing: content-box;
}

.digits {
  color: red;
  font-size: 80px;
  margin: 0;
  text-align: right;
  font-family: monospace;
}

.buttons button {
  border: none;
  border-radius: 10px;
  width: 80px;
  height: 20px;
  margin: 2px;
  color: white;
  background-color: olive;
  outline: none;
}

.line {
  border: 5px solid black;
}